
.home_banner_area {
    width: 100%;
    background: url('../../../../Images/banner/home-banner.png') no-repeat top center;
    background-position: center;
    background-size: cover;
    min-height: 890px;
}
.home_banner_area .banner_inner {
    width: 100%;
    min-height: 890px;
}
.primary_btn {
    display: inline-block;
    background: linear-gradient(to right, #1345e6 0%, #ed239f 51%, #1345e6 100%);
    color: #fff;
    letter-spacing: 0px;
    font-family: "Roboto",sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 50px;
    padding: 0 38px;
    outline: none !important;
    text-align: center;
    cursor: pointer;
    text-transform: uppercase;
    border-radius: 5px;
    background-size: 200% auto;
}
